export const getCookie = name => {
    console.log(`[getCookie] Attempting to retrieve cookie: ${name}`);
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) {
        const cookieValue = parts.pop().split(';').shift();
        console.log(`[getCookie] Cookie found: ${name}=${cookieValue}`);
        return cookieValue;
    }

    console.warn(`[getCookie] Cookie not found: ${name}`);
    return null;
};

export const getProfileProps = async (sessionId, propertiesToDisplay) => {
    console.log('[getProfileProps] Fetching profile properties...');
    if (!sessionId) {
        console.error('[getProfileProps] No session ID provided.');
        return null;
    }

    console.log('[getProfileProps] Session ID:', sessionId);

    const requiredProperties = ['firstName', 'lastName', 'email', 'profilePictureUrl'];
    const uniqueProperties = [...new Set([...propertiesToDisplay, ...requiredProperties])];

    console.log('[getProfileProps] Properties to retrieve:', uniqueProperties);

    const contextServerPublicUrl = window.digitalData?.contextServerPublicUrl;
    if (!contextServerPublicUrl) {
        console.error('[getProfileProps] Context server URL is missing.');
        return null;
    }

    const body = {
        requiredProfileProperties: uniqueProperties,
        sessionId,
        source: {
            itemId: window.digitalData?.page?.pageInfo?.pageID || 'unknown',
            itemType: 'page',
            scope: window.digitalData?.scope || 'unknown'
        }
    };

    console.log('[getProfileProps] Request body:', body);

    try {
        const response = await fetch(`${contextServerPublicUrl}/context.json`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'allow-redirects': 'false'
            },
            body: JSON.stringify(body)
        });

        console.log('[getProfileProps] HTTP status:', response.status);

        if (response.status !== 200) {
            throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const data = await response.json();

        if (data?.profileProperties) {
            console.log('[getProfileProps] Retrieved profile properties:', data.profileProperties);
            return data.profileProperties;
        }

        console.warn('[getProfileProps] No profile properties found.');
        return null;
    } catch (error) {
        console.error('[getProfileProps] Error fetching profile properties:', error);
        return null;
    }
};

export const getProperty = async itemId => {
    console.log(`[getProperty] Fetching property for itemId: ${itemId}`);
    if (!itemId) {
        const errorMessage = '[getProperty] itemId is required but was not provided.';
        console.error(errorMessage);
        throw new Error(errorMessage);
    }

    const contextServerPublicUrl = window.digitalData?.contextServerPublicUrl;
    const siteKey = window.digitalData?.scope; // Assuming `siteKey` is available globally

    if (!contextServerPublicUrl) {
        const errorMessage = '[getProperty] Context server URL is missing.';
        console.error(errorMessage);
        throw new Error(errorMessage);
    }

    const url = `${contextServerPublicUrl}/cxs/profiles/properties/${itemId}`;
    console.log(`[getProperty] Request URL: ${url}`);

    try {
        const response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Site-Key': siteKey // Add siteKey if required
            }
        });

        console.log(`[getProperty] HTTP status for itemId ${itemId}: ${response.status}`);

        // Check if the response status is not OK
        if (!response.ok) {
            throw new Error(`Failed to fetch property. HTTP status: ${response.status}`);
        }

        // Handle empty or invalid response body
        const responseBody = await response.text(); // Read response as text
        if (!responseBody) {
            console.warn(`[getProperty] Empty response for itemId ${itemId}.`);
            return {name: 'Unknown Name', cardName: 'Unknown Card', type: 'string'}; // Fallback values
        }

        const parsedBody = JSON.parse(responseBody); // Parse JSON
        const metadata = parsedBody.metadata || {};

        const name = metadata.name || 'Unknown Name';
        const cardName = getCardName(metadata.systemTags);
        const type = parsedBody.type || 'string';

        console.log(`[getProperty] Retrieved property for itemId ${itemId}:`, {name, cardName, type});
        return {name, cardName, type};
    } catch (error) {
        console.error(`[getProperty] Error fetching property for itemId ${itemId}:`, error);
        throw error; // Re-throw error to handle upstream
    }
};

// Helper function to extract cardName from systemTags
const getCardName = systemTags => {
    console.log('[getCardName] Extracting card name from system tags:', systemTags);

    if (!systemTags || !Array.isArray(systemTags)) {
        console.warn('[getCardName] System tags are invalid or missing.');
        return 'Other Data';
    }

    for (const tag of systemTags) {
        if (tag.startsWith('cardDataTag')) {
            const tagParts = tag.split('/');
            if (tagParts.length > 3) {
                const cardName = tagParts[tagParts.length - 1];
                console.log('[getCardName] Extracted card name:', cardName);
                return cardName;
            }
        }
    }

    console.warn('[getCardName] No matching cardDataTag found. Defaulting to "Main Data".');
    return 'Main Data';
};
