import {gql} from '@apollo/client';
import {CORE_NODE_FIELDS} from './fragments';

export const GetUnomiProfile = gql`
    ${CORE_NODE_FIELDS}
    query getUnomiProfile($workspace: Workspace!, $id: String!, $language: String!) {
        response: jcr(workspace: $workspace) {
            workspace
            unomiProfile: nodeById(uuid: $id) {
                ...CoreNodeFields
                title: displayName(language: $language)
                properties : property(name: "unomiProperties") {
                    values
                }
            }
        }
    }`;

