import React, {useCallback, useContext, useEffect, useState} from 'react';
import {Card, CardContent, Typography, Box, Avatar} from '@mui/material';
import PersonIcon from '@mui/icons-material/Person';
import {AppCtx} from '../../context';
import {getCookie, getProfileProps, getProperty} from '../../misc';

function waitForWem() {
    return new Promise(resolve => {
        const interval = setInterval(() => {
            if (window.wem) {
                clearInterval(interval);
                resolve(true);
            }
        }, 100); // Check every 100ms
    });
}

export const AccountProfile = props => {
    const [isWemLoaded, setIsWemLoaded] = useState(false);
    const [profileProps, setProfileProps] = useState(null);
    const [isProfileLoaded, setIsProfileLoaded] = useState(false);
    const [groupedProperties, setGroupedProperties] = useState({});

    const {content} = useContext(AppCtx);

    const propertiesToDisplay = content.properties;
    // Wait for `window.wem` to be loaded
    useEffect(() => {
        const loadWem = async () => {
            console.log('[WEM] Waiting for window.wem...');
            await waitForWem();
            setIsWemLoaded(true);
            console.log('[WEM] window.wem is loaded:', window.wem);
        };

        loadWem();
    }, []);

    // Fetch profile data once WEM is loaded
    const fetchProfileData = useCallback(async () => {
        try {
            if (isWemLoaded) {
                const sessionId = window.cxs?.sessionId || getCookie('wem-session-id');
                console.log('[Profile] Fetching profile data with sessionId:', sessionId);

                const props = await getProfileProps(sessionId, propertiesToDisplay);
                console.log('[Profile] Retrieved profile properties:', props);

                setProfileProps(props);
                setIsProfileLoaded(true);
            } else {
                console.warn('[Profile] WEM is not loaded, skipping profile fetch.');
                setIsProfileLoaded(false);
            }
        } catch (error) {
            console.error('[Profile] Error fetching profile data:', error);
        } finally {
            setIsProfileLoaded(true);
            console.log('[Profile] Profile loading status set to true.');
        }
    }, [isWemLoaded, propertiesToDisplay]);

    // Fetch grouped properties dynamically
    useEffect(() => {
        const fetchGroupedProperties = async () => {
            try {
                const grouped = {};

                console.log('[Profile] Grouping properties...');
                await Promise.all(
                    propertiesToDisplay
                        .filter(property => !['firstName', 'lastName', 'email', 'profilePictureUrl'].includes(property))
                        .map(async property => {
                            try {
                                const {name, cardName, type} = await getProperty(property);
                                const value = profileProps?.[property];

                                if (name !== 'Not Available' && value !== undefined) {
                                    if (!grouped[cardName]) {
                                        grouped[cardName] = [];
                                    }

                                    grouped[cardName].push({name, value, type});
                                }
                            } catch (error) {
                                console.error(`[Profile] Error fetching grouped property for ${property}:`, error);
                            }
                        })
                );

                console.log('[Profile] Grouped properties:', grouped);
                setGroupedProperties(grouped);
            } catch (error) {
                console.error('[Profile] Error grouping properties:', error);
            }
        };

        if (profileProps) {
            fetchGroupedProperties();
        }
    }, [profileProps, propertiesToDisplay]);

    // Wait for window.wem to load, then fetch profile properties
    useEffect(() => {
        if (isWemLoaded) {
            console.log('[Profile] WEM is loaded, fetching profile data...');
            fetchProfileData();
        }
    }, [isWemLoaded, fetchProfileData]);

    const getAvatar = avatar => {
        if (avatar) {
            return (
                <Avatar
                    src={avatar}
                    sx={{
                        height: 64,
                        mb: 2,
                        width: 64
                    }}
                />
            );
        }

        return (
            <Avatar
                sx={{
                    height: 64,
                    mb: 2,
                    width: 64
                }}
            >
                <PersonIcon fontSize="large"/>
            </Avatar>
        );
    };

    return (
        <Card
            sx={{
                display: 'flex',
                flexDirection: 'column',
                height: '100%',
                width: 350,
                borderRadius: 4,
                boxShadow: '0px 8px 24px rgba(0, 0, 0, 0.15)',
                overflow: 'hidden'
            }}
            {...props}
        >
            <CardContent sx={{flexGrow: 1}}>
                <Box
                    sx={{
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column'
                    }}
                >
                    {getAvatar(profileProps?.profilePictureUrl)}

                    <Typography gutterBottom color="textPrimary" variant="h5">
                        {`${profileProps?.firstName} ${profileProps?.lastName}`}
                    </Typography>

                    <Typography color="textSecondary" variant="body2">
                        {profileProps?.email}
                    </Typography>

                    {/* Render grouped properties */}
                    <Box
                        sx={{
                            marginTop: 2,
                            width: '100%'
                        }}
                    >
                        {Object.entries(groupedProperties).map(([cardName, properties]) => (
                            <Box key={cardName} sx={{marginBottom: 2}}>
                                <Typography
                                    sx={{fontWeight: 'bold', marginBottom: 1}}
                                    color="textPrimary"
                                    variant="body1"
                                >
                                    {cardName}
                                </Typography>
                                <Box
                                    sx={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        gap: 1 // Adds space between rows
                                    }}
                                >
                                    {properties.map(({name, value, type}, index) => {
                                        // Format date if type is "date"
                                        const formattedValue =
                                            type === 'date' ?
                                                new Intl.DateTimeFormat('en-US', {
                                                    year: 'numeric',
                                                    month: 'short',
                                                    day: '2-digit'
                                                }).format(new Date(value)) : // Format dates
                                                type === 'boolean' ?
                                                    value ?
                                                        'Yes' : // Map true to "Yes"
                                                        'No' : // Map false to "No"
                                                    value; // Default for other types

                                        return (
                                            <Box
                                                key={index}
                                                sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                    alignItems: 'center'
                                                }}
                                            >
                                                <Typography
                                                    sx={{
                                                        textAlign: 'left',
                                                        flex: 1
                                                    }}
                                                    color="textSecondary"
                                                    variant="body2"
                                                >
                                                    {name}
                                                </Typography>
                                                <Typography
                                                    sx={{
                                                        textAlign: 'right',
                                                        flex: 1,
                                                        fontWeight: 'bold'
                                                    }}
                                                    color="textPrimary"
                                                    variant="body2"
                                                >
                                                    {formattedValue}
                                                </Typography>
                                            </Box>
                                        );
                                    })}
                                </Box>
                            </Box>
                        ))}
                    </Box>
                </Box>
            </CardContent>
        </Card>
    );
};
